header {
    background-color: #9e9d9d;
    color: #000000;
    padding: 1px 3px;
    
    font-size: 20px; 
  }
  .logo{
    width: 50px;
    margin-right:30px;
  }

  nav ul {
    list-style: none;
    padding: 0;
    display: flex;
    align-items: center;
    height: 20px;
    margin-left: 70px;
  }
  
  nav ul li {
    display: inline;
    margin-right: 20px;
    margin-left: 10px;
    padding:0 7px;
  }
  
  
  .navItem:hover {
    color: #ffd700;
    padding-bottom: 3px;
    border-bottom: 1px solid #ffd700;
    cursor: pointer;
  }
  

  .search{
    margin-left: 150px;
    width: 400px;
    height: 40px;
    border-radius: 20px;
    border: none;
    font-size: 18px;
    padding: 3px 10px;
    background: #f1f1f1;
  }
 .navItem{
    text-decoration: none;
    color: rgb(32, 32, 32);
 }
 